.info{
    padding:10rem 0 12rem 0;
    &__container{
        display: flex;
        justify-content: space-between;
        @media (max-width:1100px){
            flex-wrap: wrap;
            justify-content: space-between;
        }
        &-title{
            padding-left: 3rem;
            max-width: 310px;
            width: 100%;
            font-weight: 600;
            font-size: 24px;
            line-height: 36px;
            letter-spacing: -0.04em;
            color:$text2;
        }
        &-item:first-child{
            margin-bottom: 10rem;
        }
    }
    &__box{
        display: flex;
        @media (max-width:1100px){
            flex-wrap: wrap;
        }

        &-description{
            font-style: normal;
            font-weight: 500;
            font-size: 15px;
            line-height: 22px;
            letter-spacing: -0.04em;
            color: #5F648C;
        }
       
        &-item{
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            @media (max-width:1100px){
                width: 100%;
                justify-content: start;
                flex-direction: row;
            }

        }
    }
}