@keyframes fade{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}


.modal-wrapper{
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(0,0,0,0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    display: none;
    animation: fade 1s ease;
    


}
.modal__inner-form{

    background: $bg1;
    padding:6rem;
    border-radius: 4rem;
    max-width: 750px;
    width: 100%;
    position: relative;
}
.modal__img{
    position: absolute;
    right: -44px;
    top: -44px;
    @media (max-width:768px){
        margin-top: 2rem;
        left:50%;
        top:0;
        transform: translate(-50%,-50%);
        max-width: 120px;
        width: 100%;
        height: auto;
    }
}
.modal__inner-title{
    max-width: 375px;
    width: 100%;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 36px;
    letter-spacing: -0.04em;
    color: #FFFFFF;
    margin-bottom: 37px;
    @media (max-width:768px){
        margin-top:4rem;
    }
}
.modal__inner-subtitle{
    max-width: 435px;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 30px;
    color: #FFFFFF;
    margin-bottom: 4rem;
    width: 100%;
}
.input__field{
    margin-top: 4rem;
}
.modal__close{
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 30px;
    color:$text5;
    margin: 2rem 0;
    cursor: pointer;
    position: relative;
    display: inline-block;
    &::after{
        content:"";
        position: absolute;
        width:0%;
        height: 2px;
        background: $text2;
        left:0;
        bottom:-5px;
        margin: 0 auto;
        

    }
    &:hover{
        &::after{
            transition: all 0.5s ease-in-out;
            width:100%;
        }
    }
}