.about{

    &.about__odd{
        background-color: $bg2;
        padding:25rem 0 18rem 0;
        .about__item-img{
            width:8rem;
            height:8rem;
        }
        .odd{
            padding-left: 6rem;
        }
        @media (max-width:992px){
            padding:5rem 0;
        }
    }
    &.about__even{
        
        background-color: $bg3;
        padding:10rem 0 6.5rem 0;
        .about__item-img{
            width:6rem;
            height:8rem;
        }
        .even{
            padding-left: 6rem;
            flex-direction:row-reverse;
        }
        
        @media (max-width:992px){
            padding:5rem 0;
        }
    }
    &__inner{
        display: flex;
        @media (max-width:992px){
            flex-wrap: wrap;
            &.even{
                flex-direction: column-reverse;
            }
        }

    }
    &__item{
        flex:0 0 50%;
        @media (max-width:992px){
            flex:0 0 100%;
        }
        &-img{
            @media (max-width:992px){
                display: none;
            }  
        }
        &-title{
            font-style: normal;
            font-weight: 500;
            font-size: 55px;
            line-height: 70px;
            letter-spacing: -0.04em;
            max-width: 45rem;
            color:$text1;
            margin-top: 8rem;
            @media (max-width:1500px){
                font-size: 4rem;
                line-height: 4.5rem;
                margin-top: 35px;
            }
            @media (max-width:992px){
                font-size: 3rem;
                line-height: 3.5rem;
                margin-top: 35px;
                flex:0 0 100%;
            }
            
        }
        &-text{
            font-style: normal;
            font-weight: normal;
            font-size: 20px;
            line-height: 34px;
            color: $text2;
            margin-top: 3rem;
            margin-bottom: 6rem;

        }
        &-btn{
            font-style: normal;
            font-weight: 600;
            font-size: 16px;
            line-height: 24px;
            color:$basic1;
            padding:1.8rem 7rem 1.8rem 3rem;
            border-radius: 1rem;
            background-color: #fff;
            display:inline-block;
            cursor: pointer;
            transition:all .5s ease-in-out;
            &:hover{
                background-color: $bg1;
                color:#fff;
            }
            @media (max-width:1500px){
                margin-top: 35px;
            }
            @media (max-width:500px){
                padding:12px 46px 14px 20px
            }
        }
        &.image__item{
            @media (max-width:1500px){
                max-width: 100%;
                width: 100%;
                height: auto;
            }
        }
    }
    
}