html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 34px;
  color:$text2;
}
h1,h2,h3,h4,h5,h6{
  margin: 0;
  padding: 0;
}
img {
  max-width: 100%;
}
li{
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  color:inherit;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1280px;
  width: 100%;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}
ul{
  list-style: none;
  margin:0;
  padding:0;
}
