.footer{
    background: $bg2;
    padding-top:17rem;
    padding-bottom:10rem;
    &__inner-title{
        max-width: 85rem;
        font-style: normal;
        font-weight: 600;
        font-size: 60px;
        line-height: 70px;
        text-align: center;
        letter-spacing: -0.04em;
        color: #FFFFFF;
        margin: 0 auto;
        margin-bottom: 4rem;
        @media (max-width:1200px){
            font-size: 45px;
            line-height: 50px;
        }
        @media (max-width:768px){
            font-size: 30px;
            line-height: 40px;
        }
        @media (max-width:500px){
            margin-bottom: 1rem;
            font-weight: 600;
            font-size: 30px;
            line-height: 35px;
            text-align: center;
            letter-spacing: -0.04em;
        }
        @media (max-width:374px){
            font-size: 25px;
            line-height: 30px;
        }
    }
    &__inner-form{
        background:$bg1;
        border-radius: 5rem;
        padding:16rem 8rem;
        margin-bottom: 10rem;
        @media (max-width:992px){
            padding:5rem 1.6rem;
        }
    }
    &__inner-subtitle{
        font-weight: 500;
        font-size: 14px;
        line-height: 21px;
        text-align: center;
        color: #FFFFFF;
        display: none;
        margin-bottom: 2rem;
        @media (max-width:500px){
            display: block;
        }
    }
    &__socials-box{
        display: flex;
        justify-content: center;
        margin-bottom: 4rem;
    }
    &__socials-link{
        margin-right: 4rem;
    }
    &__socials-img{
        width: 46px;
        height: 46px;
        @media (max-width:500px){
            width: 36px;
            height: 36px;
        }
    }
    
}
.footer__help{
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    color: $text4;
    display: flex;
    justify-content: center;
    margin-bottom: 3rem;
    @media (max-width:500px){
        display: none;
    }
}
.footer__svg{
    transition: all 0.3s ease-in-out;
}
.footer__svg:hover{
    fill: $basic5;
}
.input__field{
    max-width: 56rem;
    margin:0 auto;
}

.footer__list{
    display: flex;
    justify-content: center;
    @media (max-width:500px){
        justify-content: space-between;
    }
    &-item{
        font-style: normal;
        font-weight: normal;
        font-size: 13px;
        line-height: 19px;
        margin-right: 4rem;
    }
}
.disappeared{
    @media (max-width:500px){
        display: none;
    }
}
