
.header{

  background-color: $bg1;
  color: $basic6;
  padding:4rem 0 9rem 0;
  &__inner{
    &-item{
      &.image__item{
        padding-top: 7rem;
        @media (max-width:1180px){
          margin: 0 auto;
        }
      }
    }
    display: flex;
    justify-content: space-between;
    @media (max-width:1180px){
      flex-wrap: wrap;
    }
    &-title{
      font-weight: 600;
      font-size: 80px;
      line-height: 85px;
      letter-spacing: -0.04em;
      color:$basic6;
      max-width: 69rem;
      margin-bottom: 2.5rem;
      @media (max-width:1250px){
        font-size: 70px;
        line-height: 75px;
      }
      @media (max-width:1180px){
        max-width: 100%;
      }
      @media (max-width:768px){
        font-size: 45px;
        line-height: 50px;
      }
    }
    
  }
  .header__list{
    margin-bottom: 3rem;
    &-item{
      position: relative;
      font-style: normal;
      font-weight: normal;
      font-size: 28px;
      line-height: 42px;
      color:$basic6;
      padding-left: 42px;
      margin-top: 4px;
      &::before{
        content: "";
        position: absolute;
        top:10px;
        left:0;
        background-image: url(./../../img/svg/list__icon.svg);
        width: 26px;
        height: 26px;
        background-repeat: no-repeat;
        background-size: cover;
        @media (max-width:500px){
          width: 18px;
          height: 18px;
          background-size: cover;
          top:0;
        }
      }
    }
  }
  


}
.menu{
  margin-bottom: 13.7rem;
  &__list{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    align-items: center;
  }
  &__logo{
    font-weight: 600;
    font-size: 30px;
    line-height: 45px;
    letter-spacing: -0.04em;
    color:#fff;
    justify-self:start;
  }
  &__btn{
    justify-self:end;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    border:1px solid $basic6;
    padding:1.8rem 3rem;
    border-radius: 1rem;
    cursor:pointer;
  }

}
.form__wrapper{
  &.desctop__form{
    @media (max-width:1180px){
      display: none;
    }
  }
  &.mobile__form{
    display: none;
    margin-top: -10rem;
    form{
      width: 100%;
    }
    @media (max-width:1180px){
      display: flex;
    }
  }
  max-width: 60rem;
  background-color:#FBA24E;
  border-radius: 2rem;
  padding:2.5rem;
  margin-bottom: 10rem;
  @media (max-width:1180px){
    max-width: 100%;
    background-color: transparent;
  }

}
.input__field{
  position:relative;
  margin-bottom: 2rem;

  .input{
    border:none;
    background: #FFFFFF;
    box-shadow: 0px 7px 50px rgba(61, 70, 83, 0.3);
    padding:24px 20px;
    width: 100%;
    border-radius: 2rem;
    outline: none;
    @media (max-width:320px){
      padding:15px 10px;
    }
    &::placeholder{
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 24px;
      color:$text5;
      @media (max-width:575px){
        font-size:14px;
      }
    }
  }

}
.form__btn{
  position: absolute;
  right: 5px;
  top:5px;
  background: #62CF86;
  border-radius: 7px;
  padding:  1.8rem 3rem;
  border:none;
  color:$basic6;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  outline: none;
  cursor: pointer;
  @media (max-width:450px){
    font-size: 12px;
    padding:10px 10px;
    right: 5px;
    top:12px;
  }
}
.form__subtext{
  display: block;
  text-align: center;
  font-style: normal;
  font-weight: bold;
  font-size: 26px;
  line-height: 39px;
  letter-spacing: -0.01em;
  text-transform: uppercase;
  color: #93621A;
  @media (max-width:1180px){
    display: none;
  }
}
.header__info{
  display: flex;
  justify-content: space-between;
  &-item{
    display: flex;
    align-items: center;
  }
  &-img{
    margin-right: 2rem;
  }
  &-box{
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 82rem;
  }
  &-text{
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 25px;
  }
  &-descr{
    max-width: 25rem;
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 25px;
    text-align: right;  
  }
  @media (max-width:992px){
    display: none;
  }
}